// the moving store
// get part of state
import {getters} from './getters'
// actions for more commits
import {actions} from './actions'
// setter, which is mutation.
import {mutations} from './mutations'

// final state
const state = {
  // movie title
  title: 'doubanMovie', // App 
  // movingList.subjects = array
  movingList: {  // 
    subjects: []
  },
  // after search, we have subjects = array
  searchList: {  // 
    subjects: []
  },
  // search text in text box
  searchText: '', // 
  // loading movie === true
  loadingMoving: true, // loading
  // loading upcoming movie === true
  loadingUpComing: true, // loading
  // search loading === true
  searchLoading: true, // loading
  // that is for show more button......
  pageload: false, // 
  // upcoming movie, actual list
  upcomBody: {  // 
    start: 0
  },
  // actual loading a movie
  loadingDetail: true, // loading
  // top 240 movies
  ranking250: {}, // top 250
  // default city
  city: '', //  ''
  // movie detail has rating and images
  movieDetail: { // 
    rating: {},
    images: {}
  },
  // movie id....,, single movie but with others....
  id: '', // ID
  // ranking250 start page
  start: 0, // top 250
  // movie comment
  movieComment: {} // 
}

// export init state
// export getter, setter (mutation)
// actions (more commits)
export default {
  state,
  getters,
  actions,
  mutations
}



